<template>
  <div>
    <panel title="เพิ่มข้อมูลสมาชิก">
      <br />
      <p>เพิ่มข้อมูลสมาชิก</p>
      <div class="vx-row">
        <form-input
          label="ชื่อ-นามสกุล"
          v-model="form.customer_name"
          v-validate="`required`"
          :col="4"
        >
        </form-input>
        <form-input label="ที่อยู่" v-model="form.customer_address" v-validate="`required`" :col="4">
        </form-input>
        <custom-tree-select  class="my-5" url="/api/setting/master/customer" :col="4" v-model="form.setting_master_customer_id">
      </custom-tree-select>
        <form-input label="สาขา" v-model="form.customer_branch" v-validate="`required`" :col="4">
        </form-input>
      </div>
      <div align="right">
        <vs-button @click="addContact = true" color="primary" type="filled"
          >เพิ่ม</vs-button
        >
<!-- เพิ่มข้อมูลติดต่อ -->
        <vs-prompt
          title="เพิ่มข้อมูลติดต่อ"
          color="primary"
          @cancel="clearValMultiple"
          @accept="acceptInsertContact"
          @close="close"
          :is-valid="validName"
          :active.sync="addContact"
          acceptText =  "ยืนยัน"
          cancelText = "ยกเลิก"
        >
          <div class="con-exemple-prompt">
            <form-input label="ประเภท" v-model="tempContact.type"  v-validate="`required`" :col="12" />
            <form-input label="ข้อมูลติดต่อ" v-model="tempContact.value" v-validate="`required`" :col="12" />
          </div>
        </vs-prompt>
        <!-- สิ้นสุดเพิ่มข้้อมูลติดต่อ -->
      </div>
      <vs-table pagination max-items="5" search :data="form.customer_contact">
        <template slot="thead">
          <vs-th>ลำดับ</vs-th>
          <vs-th>ข้อมูลติดต่อ</vs-th>
          <vs-th>ประเภท</vs-th>
          <vs-th></vs-th>
        </template>

        <template slot-scope="{ data }">
          <vs-tr :key="indextr" v-for="(tr, indextr) in data">
            <vs-td>
              {{ indextr + 1 }}
            </vs-td>

            <vs-td>
              {{ form.customer_contact[indextr].value }}
            </vs-td>

            <vs-td>
              {{ form.customer_contact[indextr].type }}
            </vs-td>

            <vs-td>
              <div class="flex mb-3" align="center">
                <div class="w-1/6 bg-grid-color h-12">
                  <vs-button
                    radius
                    color="danger"
                    @click="openConfirmDelete(indextr)"
                    type="flat"
                    icon-pack="feather"
                    icon="icon-trash"
                  ></vs-button>
                </div>
                <div class="w-1/6 bg-grid-color-secondary h-12"></div>
              </div>
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>
      <p>แนบไฟล์</p>
      <div class="flex mb-4">
        <attached :id="data_id" :model="data_model" ref="upload"></attached>
      </div>
      <div class="vx-row">
        <div class="vx-col sm:w-/3 w-full ml-auto" align="right">
         <vs-button
            color="primary"
            size="medium"
            type="filled"
            @click="insertCustomer()"
            >ยืนยัน</vs-button
          >&nbsp;
          <router-link to="/member/index">
            <vs-button color="danger" type="filled" class="mb-2"
              >ยกเลิก</vs-button
            ></router-link
          >
        </div>
      </div>
    </panel>
    <br />
  </div>
</template>

<script>
import vSelect from 'vue-select'
export default {
  components:{
    'v-select': vSelect
  },
  data () {
    return {
      addContact: false,
      valMultipe: {
        value1: '',
        value2: ''
      },
      data_id: '',
      data_model: 'App\\Models\\Customer',
      form: {
        code: '1234',
        customer_name: '',
        customer_address: '',
        customer_branch: '',
        setting_master_customer_id:'',
        customer_contact: []
      },
      settingCustomer:[],
      tempContact: {
        type: '',
        value: ''
      }
    }
  },

  computed: {
    validName () {
      return (
        this.tempContact.type.length > 0 && this.tempContact.value.length > 0
      )
    }
  },
  methods: {
    async insertCustomer () {
      try {
        const result = await this.$validator.validateAll()
        if (result) {
          await this.$vs.dialog({
            type: 'confirm',
            color: 'success',
            title: 'คุณต้องการเพิ่มข้อมูลหรือไม่',
            text: `เพิ่มข้อมูลสมาชิกชื่อ ${this.form.customer_name}`,
            acceptText: 'ยืนยัน',
            cancelText: 'ยกเลิก',
            accept: this.acceptInsertTest
          })
        } else {
          await this.$vs.dialog({
            color: 'danger',
            title: 'สถานะการเพิ่มข้อมูล',
            text: 'เพิ่มข้อมูลพนักงานไม่สำเร็จ',
            acceptText: 'ปิด'
          })
        }
      } catch (error) {
        console.log('err', error)
      }
    },
    async acceptInsertTest () {
      try {
        const resp = await this.$http.post('/api/customers', this.form)
        this.data_id = resp.data.data.id
        this.$refs.upload.id = resp.data.data.id

        const first = await this.$refs.upload.submit()
        console.log(first)

        await this.$vs.notify({
          color: 'success',
          title: 'ยืนยันการเพิ่มข้อมูล',
          text: 'เพิ่มข้อมูลสำเร็จ'
        })
        await this.$router.push({ name: 'customer_index', query: {} })
      } catch (error) {
        throw error
      }
    },
    openConfirmDelete (indextr) {
      this.tempDeleteIndex = indextr
      this.deleteCustomerContact = this.form.customer_contact[indextr].value
      this.$vs.dialog({
        type: 'confirm',
        color: 'danger',
        title: 'คุณต้องการลบข้อมูลใช่หรือไม่',
        text: `ลบข้อมูลติดต่อ ${this.deleteCustomerContact}`,
        accept: this.acceptDelete,
        acceptText: 'ยืนยัน',
        cancelText: 'ยกเลิก'
      })
    },
    async acceptDelete () {
      this.form.customer_contact.splice(this.tempDeleteIndex, 1)

      await this.$vs.notify({
        color: 'danger',
        title: 'ยืนยันการลบข้อมูล',
        text: 'ลบข้อมูลสำเร็จ'
      })
    },
    acceptInsertContact () {
      this.form.customer_contact.push({ ...this.tempContact })
      this.$vs.notify({
        color: 'success',
        title: 'ยืนยันการเพิ่มข้อมูล',
        text: 'เพิ่มข้อมูลสำเร็จ'})
      this.tempContact.type = ''
      this.tempContact.value = ''
    },
    acceptAlert2 () {
      this.$vs.notify({
        color: 'success',
        title: 'สำเร็จ',
        text: 'เพิ่มข้อมูลติดต่อสำเร็จ'
      })
    },
    close () {
      this.$vs.notify({
        color: 'danger',
        title: 'ยกเลิก',
        text: 'ยกเลิกการเพิ่มข้อมูลติดต่อ'
      })
    },
    clearValMultiple () {
      this.tempContact.type = ''
      this.tempContact.value = ''
    },
    async acceptAlert () {
      const resp = await this.$http.post('/api/customers/', this.form)
      this.data_id = resp.data.data.id
      this.$refs.test.id = resp.data.data.id
      const first = await this.$refs.test.submit()
      console.log(first)
      this.$router.push({ name: 'customer_index', query: {} })
    }
  },
  mounted () {
  }
}
</script>

